<template>
<div class="liquidity" @click="hideDropdowns">
    <div class="title">{{$localizeFilter('liquidity_title')}}</div>
    <div class="subtitle">{{$localizeFilter('liquidity_subtitle')}}</div>
    <LargeToggle :title="null" :variants="poolTypes" @toggle="toggle" class="toggle"/>
    <div class="pool-amount-container">
        <div class="pool-amount first-block">
        <AmountView class="pool-amount-first" :title="$localizeFilter('amount_income')" :amount="totalIncome" :type="'income'" :color="'type-green'" :classOverride="'withdraw-available'" :availableProfitToWithdraw="availableProfitToWithdraw" @reloadData="reloadData"/>
        <!-- <AmountView v-if="selectedPoolType==='my_pools'" :title="$localizeFilter('amount_test')" :amount="testBalance" :color="'type-gray'"/>    -->
        <AmountView v-if="selectedPoolType==='my_pools'" class="pool-amount-first" :title="$localizeFilter('pools_amount_deposited')" :amount="totalDepositedInPools" :color="'type-gold'"/>
        </div>
        <div v-if="selectedPoolType==='my_pools'" class="pool-amount with-buttons">
        <!-- <AmountView class="pool-amount-first" :title="$localizeFilter('pools_amount_deposited')" :amount="totalDepositedInPools" :color="'type-gold'"/> -->
        <AmountView v-if="selectedPoolType==='my_pools'" :title="$localizeFilter('amount_test')" :amount="testBalance" :color="'type-gray'"/>   
        <div class="buttons-flex">
            <div class="buttons-flex-row">
                <div class="wrapper" style="margin-right:29px;">
                    <button class="basic-button button-first" @click.stop="showDepositOptions">{{$localizeFilter('liquidity_deposit_button')}}</button>
                    <LiquidityProviderDepositOptions class="deposit-options" v-if="showDepositDropdown" @selectOption='selectDepositOption' @close="showDepositDropdown=false"/>
                </div>   

                <div class="wrapper">
                    <button class="basic-button button-second" @click.stop="showWithdrawOptions">{{$localizeFilter('liquidity_withdraw_button')}}</button>
                    <LiquidityProviderWithdrawOptions class="withdraw-options" v-if="showWithdrawDropdown" @selectOption='selectWithdrawOption' @close="showWithdrawDropdown=false"/>
                </div>   
                
            </div>
            <div class="buttons-flex-row">
                <button class="basic-button inactive button-first">{{$localizeFilter('credit')}}</button>
                <button class="basic-button inactive button-second">{{$localizeFilter('reinvest')}}</button>
            </div>
        </div>   
        </div>
    </div>
    <div class="pool-amount first-block">
      <AmountView class="pool-amount-first" :title="$localizeFilter('amount_referral_income')" :amount="totalReferralIncome" :type="'income'" :color="'type-green'"/>
      <AmountView class="pool-amount-first" :title="$localizeFilter('gifts_to_referrals')" :amount="referralGiftsBalance" :type="'gift'" :color="'type-red'" :counterType="'gift'" v-if="selectedPoolType==='my_pools' && allowed" @showGiftModal="showGiftModal=true"/>
    </div>
    <div class="pool-amount first-block">
      <AmountView class="pool-amount-first" :title="$localizeFilter('amount_credit_deposit_income')" :amount="totalCreditDepositIncome" :type="'income'" :color="'type-green'"/>
      <AmountView class="pool-amount-first" :title="$localizeFilter('credit_deposit_amount_deposited')" :amount="creditDepositBalance" :color="'type-gold'" v-if="selectedPoolType==='my_pools'" />
    </div>
    <div class="pool-amount first-block" v-if="allowed">
      <AmountView class="pool-amount-first" :title="$localizeFilter('referral_amount_credit_deposit_income')" :amount="totalReferralCreditDepositIncome" :type="'income'" :color="'type-green'"/>
      <AmountView class="pool-amount-first" :title="$localizeFilter('referral_credit_deposit_amount_income_from_exchange')" :amount="referralCreditDepositBalance" :color="'type-gold'" v-if="selectedPoolType==='my_pools'" />
    </div>
    <!-- <LiquidityProviderToggle :class="'all-pools-toggle'" v-if="selectedPoolType==='all_pools'" :title="null" :variants="allPoolsTypes" :selected="allPoolsSelectedType" @toggle="toggleAllPoolsType" /> -->
    <div class="pools-container">
        <div v-for="(pool, idx) in pools" :key="idx">
            <LiquidityProviderPool :pool="pool" @deposit="deposit" @withdraw="withdraw"/>  
        </div>
    </div>
    <LiquidityBalanceDepositModal v-if="showBalanceDepositModal" :source="depositSource" @closeModal="closeModals"/>
    <LiquidityBalanceWithdrawModal v-if="showBalanceWithdrawModal" :withdrawToWallet="withdrawToWallet" @closeModal="closeModals"/>
    <LiquidityDepositModal v-if="showDepositModal" :pool="currentPool" :testBalance="testBalance" @closeModal="closeModals"/>
    <LiquidityWithdrawModal v-if="showWithdrawModal" :pool="currentPool" @closeModal="closeModals"/>
    <LiquidityBalanceDepositFromExternalWallet v-if="showDepositFromExternalWalletModal" @closeModal="closeModals"/>
    <Modal v-if="showWithdrawToExternalWalletModal" :type="'liquidityBalanceWithdraw'" :fromProviding="true" @closeModal="closeModals" @showWithdrawalConfirmation="showWithdrawalConfirmation"/>
    <BonusWithdrawalConfirmationModal v-if="showWithdrawalConfirmationModal"  @closeModal="closeConfirmationModal" :withdrawId="withdrawId" :commission="commission" :coinTicker="'USDT'" />
    <GiftModal v-if="showGiftModal" @closeModal="closeModals" :referralGiftsBalance="referralGiftsBalance" />
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import LargeToggle from "@/components/LargeToggle.vue"
import LiquidityProviderPool from '../components/LiquidityProviderPool.vue'
import AmountView from '../components/AmountView.vue'
import LiquidityDepositModal from '../components/newModals/LiquidityDepositModal.vue'
import LiquidityWithdrawModal from '../components/newModals/LiquidityWithdrawModal.vue'
import LiquidityBalanceDepositModal from '../components/newModals/LiquidityBalanceDepositModal.vue'
import LiquidityBalanceWithdrawModal from '../components/newModals/LiquidityBalanceWithdrawModal.vue'
import LiquidityProviderDepositOptions from '../components/LiquidityProviderDepositOptions.vue'
import LiquidityProviderWithdrawOptions from '../components/LiquidityProviderWithdrawOptions.vue'
import LiquidityBalanceDepositFromExternalWallet from '../components/newModals/LiquidityBalanceDepositFromExternalWallet.vue'
import BonusWithdrawalConfirmationModal from "@/components/newModals/BonusWithdrawalConfirmationModal.vue"
import LiquidityProviderToggle from '../components/LiquidityProviderToggle.vue'
import Modal from '../components/modals/Modal.vue'
import GiftModal from '../components/newModals/GiftModal.vue'

let allowedExchange = new Set([
  2, 49018, 1529, 22934 //, 4
])

export default defineComponent({
    components: {
        LargeToggle,
        LiquidityProviderPool,
        AmountView,
        LiquidityDepositModal,
        LiquidityWithdrawModal,
        LiquidityBalanceDepositModal,
        LiquidityBalanceWithdrawModal,
        LiquidityProviderDepositOptions,
        LiquidityProviderWithdrawOptions,
        LiquidityBalanceDepositFromExternalWallet,
        Modal,
        BonusWithdrawalConfirmationModal,
        LiquidityProviderToggle,
        GiftModal
    },
    setup() {
    },
    data: () => ({
        poolTypes: ['all_pools', 'my_pools'],
        selectedPoolType: 'all_pools',
        showDepositModal: false,
        showWithdrawModal: false,
        showBalanceDepositModal: false,
        showBalanceWithdrawModal: false,
        currentPool: null,
        interval: null,
        showDepositDropdown: false,
        depositSource: null, // wallet, bonusAccount, externalWallet
        showDepositFromExternalWalletModal: false,
        showWithdrawDropdown: false,
        withdrawToWallet: false,
        showWithdrawToExternalWalletModal: false,
        showWithdrawalConfirmationModal: false,
        showGiftModal: false,
        // referralGiftsBalance: 0,
    }),
    computed: {
        liquidityProvidingData() {
            return this.$store.getters.liquidityProvidingData
        },
        totalIncome() {
            return this.liquidityProvidingData?.totalIncome || 0
        },
        totalReferralIncome() {
            return this.liquidityProvidingData?.referralIncome || 0
        },
        testBalance() {
            return this.liquidityProvidingData?.testBalance || 0
        },
        totalCreditDepositIncome() {
            return this.liquidityProvidingData?.creditDepositIncome || 0
        },
        creditDepositBalance() {
            return this.liquidityProvidingData?.creditDepositBalance || 0
        },
        totalReferralCreditDepositIncome() {
            return this.liquidityProvidingData?.referralCreditDepositIncome || 0
        },
        referralCreditDepositBalance() {
            return this.liquidityProvidingData?.referralCreditDepositBalance || 0
        },
        referralGiftsBalance(){
            return this.liquidityProvidingData?.referralGiftsBalance || 0
        },
        totalDepositedInPools() {
            return this.liquidityProvidingData?.totalDepositedInPools || 0
        },
        pools() {
            if (this.selectedPoolType === 'all_pools') {
                return this.liquidityProvidingData?.allPools || []
            } else if (this.selectedPoolType === 'my_pools') {
                return this.liquidityProvidingData?.myPools || []
            } else {
                return []
            }
        },
        allowed() {
            return allowedExchange.has(+localStorage.exchangeClientId)
        },
        availableProfitToWithdraw() {
            return (this.liquidityProvidingData?.availableProfitToWithdraw || 0) / 1000000.0
        },
        allPoolsTypes() {
            return this.$store.getters.liquidityProviderAllPoolsTypes
        },
        allPoolsSelectedType() {
            return this.$store.getters.liquidityProviderAllPoolsSelectedType
        },
    },
    methods: {
        toggleAllPoolsType(index) {
            const data = {value: this.allPoolsTypes[index]}
            this.$store.dispatch('setLiquidityProviderAllPoolsSelectedType', data)
        },
        showDepositOptions() {
            this.showDepositDropdown = true
            this.showWithdrawDropdown = false
        },
        showWithdrawOptions() {
            this.showDepositDropdown = false
            this.showWithdrawDropdown = true
        },
        hideDropdowns() {
            this.showDepositDropdown = false
            this.showWithdrawDropdown = false
        }, 
        selectDepositOption(option) {
            if (option === 'from-wallet') {
                this.depositSource = 'wallet'
                this.showBalanceDepositModal = true
            } else if (option === 'from-bonus-account') {
                this.depositSource = 'bonusAccount'
                this.showBalanceDepositModal = true
            } else if (option === 'from-external-wallet') {
                this.depositSource = 'externalWallet'
                this.showDepositFromExternalWalletModal = true
            }
        },
        selectWithdrawOption(option) {
            if (option === 'to-wallet') {
                this.withdrawToWallet = true
                this.showBalanceWithdrawModal = true
            } else if (option === 'to-bonus-account') {
                this.withdrawToWallet = false
                this.showBalanceWithdrawModal = true
            } else if (option === 'to-external-wallet') {
                this.withdrawToWallet = false
                this.showWithdrawToExternalWalletModal = true
            }
        },
        deposit(pool) {
            this.currentPool = pool
            this.showDepositModal = true
        },
        withdraw(pool) {
            this.currentPool = pool
            this.showWithdrawModal = true
        },
        closeModals() {
            this.showDepositModal = false
            this.showWithdrawModal = false
            this.showBalanceDepositModal = false
            this.showBalanceWithdrawModal = false
            this.currentPool = null
            this.depositSource = null
            this.withdrawToWallet = false
            this.showDepositFromExternalWalletModal = false
            this.showWithdrawToExternalWalletModal = false
            this.showGiftModal = false
            this.loadData()
        },
        showWithdrawalConfirmation(withdrawBalanceRequestId, fee) {
            this.closeModal()
            this.withdrawId = withdrawBalanceRequestId
            this.commission = fee || 0
            setTimeout(()=>{
                this.showWithdrawalConfirmationModal = true
            })
        },
        closeModal() {
            this.showWithdrawToExternalWalletModal = false
            // this.clearAll()
        },
        closeConfirmationModal() {
            this.showWithdrawalConfirmationModal = false
            this.closeModal()
        },
        toggle(index) {
            this.selectedPoolType = this.poolTypes[index]
        },
        async reloadData() {
            await this.loadData(true)
        },
        async loadData(silentCatch) {
            const data = {allPoolsSelectedType: this.allPoolsSelectedType}
            try {
                await this.$store.dispatch('getLiquidityProvidingData', data)
            }
            catch (e) {
                if(!silentCatch) {
                    this.$error(e)
                }
            } 
            
            // try {
            //     this.referralGiftsBalance = await this.$store.dispatch('getReferralGiftsBalance')
            // }
            // catch (e) {
            //     this.$error(e)
            // }
        }
  },
  async mounted() {
    await this.loadData()
    this.interval = setInterval(async () => {
        await this.loadData()
    }, 3000)
  },
  unmounted() {
    clearInterval(this.interval)
    this.interval = null
  }  
})
</script>

